import { NgModule } from '@angular/core';
import { StoreModule } from '@ngrx/store';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { PagesRoutingModule } from './home-routing.module';
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import { SharedModule } from './../shared/shared.module';
import { SharedLazyModule } from './../shared/shared-lazy.module';
// Components
import { ResetComponent } from './components/reset/reset.component';
import { LoginComponent } from './components/index/login/login.component';
import { NewsletterComponent } from './components/newsletter/newsletter.component';
import { CustomerSpaceLinksComponent } from './components/index/customer_space_links/customer-space-links.component';
import { AdvantagesBesComponent } from './components/index/advantages_bes/advantages-bes.component';


@NgModule({
  imports: [
    PagesRoutingModule,
    FormsModule,
    SharedLazyModule,
    BsDropdownModule.forRoot(),
    CommonModule,
    ReactiveFormsModule
  ],
  declarations: [
    ResetComponent,
    NewsletterComponent,
    LoginComponent,
    CustomerSpaceLinksComponent,
    AdvantagesBesComponent,
  ],
  providers: []
})
export class HomeModule { }



