import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';


@Component({
  moduleId: module.id,
  templateUrl: 'reset.component.html',
  providers: []
})

export class ResetComponent implements OnInit {
  model: any = {};
  loading = false;
  error = '';
  success = false;

  constructor(
    private router: Router,
  ) { }

  ngOnInit() {
    // reset login status
  }

  reset() {
    this.loading = false;
    this.success = true;
  }
}


